<template>
    <yd-layout>
        <div class="tree-menu">
            <trs-tree :type="type" v-on:callbackTop="addFocus" @click="addFlag = !addFlag"></trs-tree>
        </div>
        <yd-tabbar slot="tabbar" bgcolor="#fff">
            <div class="disagree" @click="$router.go(-1)">确定</div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
import trsTree from '../../components/util/trsTree/trsTree.vue';
export default {
    name: 'tripTree',
    components: {
        trsTree
    },
    data() {
        return {
            addFlag: false,
            formJson: {},
            type: 'addTrip',
            othertravelnames: ''
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('出差审批');
        vm.othertravelnames = vm.getTravelInfo.othertravelnames;
    },
    computed: {
        ...mapGetters(['getTravelInfo'])
    },
    methods: {
        ...mapMutations([
            'setTravelInfo'
        ]),
        // 随行人员
        addFocus(json) {
            let vm = this;
            if (vm.othertravelnames == '') {
                for (let key in json) {
                    for (let key1 in json[key]) {
                        vm.othertravelnames = key1 + '、' + vm.othertravelnames;
                    }
                }
                vm.othertravelnames = vm.othertravelnames.substring(0, vm.othertravelnames.length - 1);
                vm.setTravelInfo({
                    othertravelnames: vm.othertravelnames
                })
            } else {
                console.log(json)
                let nameArr = [];
                nameArr = vm.othertravelnames.split('、');
                for (let key in json) {
                    for (let key1 in json[key]) {
                        for (let i = 0; i < nameArr.length; i++) {
                            if (nameArr[i] == key1 && json[key][key1] == 0) {
                                nameArr.splice(i, 1);
                            } else if (nameArr[i] != key1 && json[key][key1] == 1) {
                                nameArr.push(key1);
                            }
                        }
                    }
                }
                nameArr = Array.from(new Set(nameArr));
                vm.othertravelnames = nameArr.join('、');
                vm.setTravelInfo({
                    othertravelnames: vm.othertravelnames
                })
            }
        }
    }
}
</script>
<style lang="less" scoped>
.disagree {
    flex: 1;
    color: #fff;
    font-size: .36rem;
    line-height: 1rem;
    text-align: center;
    background-color: #59a5fd;
}
</style>


